import React, { useState } from 'react'

const Contact = () => {
  const [data, setdata]=useState({
    Fname:"",
    phone:"",
    email: "",
    msg:"",
  });
  const inputevent=(event)=>{
    const {name,value}=event.target;
    setdata((prev)=>{
      return {
        ...prev,
        [name]:value,
      }
    })
  }
  const formSubmit=(e)=>{
    e.preventDefault();
    alert(`My name is ${data.Fname}. My Phone number is ${data.phone}. My Email id is ${data.email} and message to you is ${data.msg}`);

  }
  return (
    <React.Fragment>
      <div className="my-5">
        <h1 className="text-center">Contact us</h1>
      </div>
      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto">
            <form onSubmit={formSubmit}>
              <div className="mb-3">
                <label for="exampleFormControlInput1" className="form-label">Full Name</label>
                <input type="text" className="form-control" name="Fname" value={data.Fname} onChange={inputevent} placeholder="Enter Your Full Name" />
              </div>
              <div className="mb-3">
                <label for="exampleFormControlInput1" className="form-label">Email Address</label>
                <input type="email" className="form-control" name="email" value={data.email} onChange={inputevent} placeholder="name@example.com" />
              </div>
              <div className="mb-3">
                <label for="exampleFormControlInput1" className="form-label">Phone Number</label>
                <input type="number" className="form-control" name="phone" value={data.phone} onChange={inputevent} placeholder="Enter Your Phone Number" />
              </div>
              <div className="mb-3">
                <label for="exampleFormControlTextarea1" className="form-label">Message....</label>
                <textarea className="form-control" name="msg" value={data.msg} onChange={inputevent} rows="3"></textarea>
              </div>
              <div className="mb-3">
                <button className="btn btn-primary" type="submit">Submit form</button>
              </div>
            </form>
          </div>
        </div>
      </div>

    </React.Fragment>
  )
}
export default Contact; 